// src/components/Home.jsx
import React, { useEffect, useMemo, useRef, useState } from "react";
import api, { authStore } from "../Api/Api";
import logo from "../logo/logo.png";

const ME_URL  = "me/";
const WHS_URL = "warehouses/";

const LS_TRACKS = "lc_tracks";
const norm = (s) => String(s ?? "").trim();
const sanitizeTrack = (s) => norm(String(s).replace(/\s+/g, ""));
const TN_MIN = 1;
const TN_MAX = 32;

const CopyIco = (p) => (
  <svg viewBox="0 0 24 24" width="16" height="16" fill="none" stroke="currentColor" strokeWidth="2" {...p}>
    <rect x="9" y="9" width="13" height="13" rx="2"/><path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"/>
  </svg>
);
const SearchIco = (p) => (
  <svg viewBox="0 0 24 24" width="18" height="18" fill="none" stroke="currentColor" strokeWidth="2" {...p}>
    <circle cx="11" cy="11" r="7"/><path d="M21 21l-3.5-3.5"/>
  </svg>
);

const Home = () => {
  const [loading, setLoading] = useState(true);

  // профиль / склад
  const [clientCode, setClientCode] = useState("");
  const [deptText, setDeptText]     = useState("");
  const [cnAddress, setCnAddress]   = useState("");
  const [cnPhone, setCnPhone]       = useState("");

  // track input + подсказки
  const [track, setTrack]     = useState("");
  const [open, setOpen]       = useState(false);
  const [suggest, setSuggest] = useState([]);
  const [visible, setVisible] = useState(12);
  const [error, setError]     = useState("");

  const wrapRef  = useRef(null);
  const inputRef = useRef(null);

  useEffect(() => {
    let alive = true;
    (async () => {
      setLoading(true);
      try {
        const { data: me } = await api.get(ME_URL);

        const code = me?.client_code_display || me?.client_code || "";
        setClientCode(code);

        // ❗️НЕ добавляем клиентский код в отдел
        const dept = me?.cn_warehouse_address ? String(me.cn_warehouse_address) : "";
        setDeptText(dept);

        const whId = me?.pickup_point?.default_cn_warehouse?.id;
        if (whId) {
          const { data } = await api.get(`${WHS_URL}${whId}/`);
          if (!alive) return;
          setCnAddress(data?.address_cn || "");
          setCnPhone(data?.contact_phone || "");
        } else {
          const { data } = await api.get(WHS_URL, { params: { is_active: true } });
          const list = Array.isArray(data?.results) ? data.results : [];
          const first = list[0] || {};
          if (!alive) return;
          setCnAddress(first?.address_cn || "");
          setCnPhone(first?.contact_phone || "");
        }
      } catch (e) {
        if (e?.response?.status === 401) {
          authStore.clear();
          window.location.replace("/login");
          return;
        }
      } finally {
        if (alive) setLoading(false);
      }
    })();
    return () => { alive = false; };
  }, []);

  useEffect(() => {
    try {
      const raw = localStorage.getItem(LS_TRACKS);
      const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
      setSuggest(arr);
    } catch { setSuggest([]); }
  }, []);

  useEffect(() => {
    const onDoc = (e) => { if (wrapRef.current && !wrapRef.current.contains(e.target)) setOpen(false); };
    const onEsc = (e) => { if (e.key === "Escape") setOpen(false); };
    document.addEventListener("mousedown", onDoc);
    document.addEventListener("keydown", onEsc);
    return () => {
      document.removeEventListener("mousedown", onDoc);
      document.removeEventListener("keydown", onEsc);
    };
  }, []);

  const filtered = useMemo(() => {
    const q = norm(track).toLowerCase();
    if (!q) return suggest;
    return suggest.filter((s) => String(s).toLowerCase().includes(q));
  }, [suggest, track]);

  const shown = useMemo(() => filtered.slice(0, visible), [filtered, visible]);
  useEffect(() => setVisible(12), [track, open]);

  const onScroll = (e) => {
    const el = e.currentTarget;
    if (el.scrollTop + el.clientHeight >= el.scrollHeight - 6) {
      setVisible((v) => Math.min(v + 12, filtered.length));
    }
  };

  const validateTrack = (t) => {
    if (!t) return "Введите трек-номер.";
    if (t.length < TN_MIN || t.length > TN_MAX) return `Длина трек-номера: ${TN_MIN}-${TN_MAX} символов.`;
    return "";
  };

  const goCheck = (e) => {
    e.preventDefault();
    const t = sanitizeTrack(track);
    const err = validateTrack(t);
    setError(err);
    if (err) {
      inputRef.current?.focus();
      return;
    }
    try {
      const raw = localStorage.getItem(LS_TRACKS);
      const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
      const next = [t, ...arr.filter((x) => x !== t)].slice(0, 200);
      localStorage.setItem(LS_TRACKS, JSON.stringify(next));
    } catch {}
    window.location.href = `/parcels?track=${encodeURIComponent(t)}`;
  };

  const copyCode = async () => { try { await navigator.clipboard.writeText(clientCode); } catch {} };
// стало:
const copyAll = async () => {
  const txt = `${deptText || "—"}`; // копируем только раздел "Отдел"
  try { await navigator.clipboard.writeText(txt); } catch {}
};

  return (
    <div className="home">
      <div className="home__card">
        <div className="home__head">
          <div className="home__code">
            <div className="home__sub">Ваш клиентский код</div>
            <div className="home__chip">
              <span className="home__chip-text">{clientCode || "—"}</span>
              <button className="home__chip-btn" type="button" onClick={copyCode} aria-label="Скопировать код">
                <CopyIco/>
              </button>
            </div>
          </div>
          <img src={logo} alt="Lider Cargo" className="home__brand" />
        </div>

        <form className="home__track" onSubmit={goCheck} autoComplete="off" ref={wrapRef}>
          <div className="home__label">Отследить товар</div>
          <div className="home__track-wrap">
            <label className="visually-hidden" htmlFor="home-track">Трек-номер</label>
            <input
              id="home-track"
              ref={inputRef}
              className={`home__input ${error ? "is-invalid" : ""}`}
              placeholder="Введите трек-номер"
              value={track}
              onChange={(e) => {
                const v = e.target.value.replace(/\s+/g, "");
                setTrack(v);
                if (error) setError("");
              }}
              onFocus={() => setOpen(true)}
              maxLength={TN_MAX}
              aria-invalid={!!error}
              aria-describedby={error ? "home-track-err" : undefined}
              onKeyDown={(e) => { if (e.key === "Enter") goCheck(e); }}
            />
            <button className="home__search-ico" type="submit" aria-label="Проверить">
              <SearchIco/>
            </button>

            {open && (
              <div className="home__menu" role="listbox" onMouseLeave={() => setOpen(false)}>
                <div className="home__list" onScroll={onScroll}>
                  {shown.length === 0 ? (
                    <div className="home__empty">Нет подсказок</div>
                  ) : shown.map((s, i) => (
                    <div
                      key={`${s}-${i}`}
                      className="home__option"
                      role="option"
                      onMouseDown={() => { setTrack(String(s)); setOpen(false); setError(""); inputRef.current?.focus(); }}
                    >
                      {s}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
          <div className="home__hint">Длина трек-номера {TN_MIN}–{TN_MAX} символов, без пробелов.</div>
          {error ? <div id="home-track-err" className="home__error" role="alert" aria-live="polite">{error}</div> : null}
        </form>

        <div className="home__label">Адрес</div>
        {loading ? (
          <div className="home__loading">Загрузка…</div>
        ) : (
          <div className="home__addr">
            {/* ❌ Убрали дублирующую строку "Ваш код" */}
            <div className="home__row"><div className="home__row-key">Телефон:</div><div className="home__row-val">{cnPhone || "—"}</div></div>
            <div className="home__row"><div className="home__row-key">Адрес:</div><div className="home__row-val">{cnAddress || "—"}</div></div>
            <div className="home__row">
              <div className="home__row-key">Отдел:</div>
              <div className="home__row-val">{deptText ? deptText.split("\n").map((l, i) => <div key={i}>{l}</div>) : "—"}</div>
            </div>
          </div>
        )}

        <button className="home__copy" type="button" onClick={copyAll}>Скопировать</button>
      </div>
    </div>
  );
};

export default Home;
