// src/components/Parcels/Parcelsscan.jsx
import React, { useEffect, useMemo, useRef, useState } from "react";
import api from "../Api/Api";
import logo from "../logo/logo.png";

/* ==== API ==== */
const SCAN_URL = "orders/scan/";   // POST { tracking_number, [description] }
const FIND_URL = "orders/find/";   // GET   ?q=<string>

/* ==== swagger-driven limits ==== */
const TN_MIN = 1;
const TN_MAX = 32;

/* ==== utils ==== */
const norm  = (s) => String(s ?? "").trim();
const uniq  = (arr) => Array.from(new Set(arr.map(String)));
const clamp = (n, a, b) => Math.max(a, Math.min(b, n));
const sanitizeTN = (s) => norm(String(s).replace(/\s+/g, "")); // убрать пробелы внутри

/* локальная история для подсказок */
const LS_TRACKS = "lc_tracks";

/* ===== Typeahead инпут (1 поле) ===== */
const TrackInput = ({ value, onChange, suggestionsPool, onEnter, hasError }) => {
  const [open, setOpen] = useState(false);
  const [serverHints, setServerHints] = useState([]);
  const [visible, setVisible] = useState(12);
  const wrapRef = useRef(null);
  const inputRef = useRef(null);

  // клик снаружи / Esc — закрыть меню
  useEffect(() => {
    const onDoc = (e) => { if (wrapRef.current && !wrapRef.current.contains(e.target)) setOpen(false); };
    const onEsc = (e) => { if (e.key === "Escape") setOpen(false); };
    document.addEventListener("mousedown", onDoc);
    document.addEventListener("keydown", onEsc);
    return () => {
      document.removeEventListener("mousedown", onDoc);
      document.removeEventListener("keydown", onEsc);
    };
  }, []);

  // серверные подсказки
  useEffect(() => {
    let alive = true;
    const q = norm(value);
    if (!q) { setServerHints([]); setVisible(12); return; }
    (async () => {
      try {
        const { data } = await api.get(FIND_URL, { params: { q } });
        if (!alive) return;
        const list = Array.isArray(data) ? data : (Array.isArray(data?.results) ? data.results : []);
        const hints = list.map((x) => (typeof x === "string" ? x : x?.tracking_number)).filter(Boolean);
        setServerHints(hints);
        setVisible(12);
      } catch { /* silent */ }
    })();
    return () => { alive = false; };
  }, [value]);

  const suggestions = useMemo(() => {
    const base = uniq([...(suggestionsPool || []), ...(serverHints || [])]);
    const q = norm(value).toLowerCase();
    return q ? base.filter((t) => String(t).toLowerCase().includes(q)) : base;
  }, [suggestionsPool, serverHints, value]);

  const shown = useMemo(() => suggestions.slice(0, visible), [suggestions, visible]);

  const onScroll = (e) => {
    const el = e.currentTarget;
    if (el.scrollTop + el.clientHeight >= el.scrollHeight - 6) {
      setVisible((v) => clamp(v + 12, 0, suggestions.length));
    }
  };

  return (
    <div className="parcels__track-row" ref={wrapRef}>
      <input
        ref={inputRef}
        className={`parcels__input ${hasError ? "is-invalid" : ""}`}
        placeholder="Трек-номер"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        onFocus={() => setOpen(true)}
        onKeyDown={(e) => {
          if (e.key === "Enter") {
            e.preventDefault();
            onEnter?.();
          }
        }}
        autoComplete="off"
        inputMode="text"
        maxLength={TN_MAX}
        aria-expanded={open}
        aria-haspopup="listbox"
        aria-invalid={!!hasError}
      />
      {open && (
        <div className="parcels__menu" role="listbox" onMouseLeave={() => setOpen(false)}>
          <div className="parcels__list" onScroll={onScroll}>
            {shown.length === 0 ? (
              <div className="parcels__empty">Нет подсказок</div>
            ) : shown.map((s, i) => (
              <div
                key={`${s}-${i}`}
                className="parcels__option"
                role="option"
                onMouseDown={() => { onChange(String(s)); setOpen(false); inputRef.current?.focus(); }}
              >
                {s}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

/* ===== Главный компонент (скан) ===== */
const Parcelsscan = () => {
  const [value, setValue] = useState("");
  const [history, setHistory] = useState([]);
  const [submitting, setSubmitting] = useState(false);
  const [msg, setMsg] = useState("");
  const [err, setErr] = useState("");
  const [createdEvent, setCreatedEvent] = useState(null); // created_event из swagger

  // история из LS
  useEffect(() => {
    try {
      const raw = localStorage.getItem(LS_TRACKS);
      const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
      setHistory(arr);
    } catch { setHistory([]); }
  }, []);

  // ?track=... автозаполнение
  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const tn = norm(params.get("track"));
    if (tn) setValue(tn);
  }, []);

  const suggestionsPool = useMemo(() => history, [history]);

  const validateTN = (tn) => {
    if (!tn) return "Введите трек-номер.";
    if (tn.length < TN_MIN || tn.length > TN_MAX) return `Длина трек-номера должна быть от ${TN_MIN} до ${TN_MAX} символов.`;
    return "";
  };

  const doSubmit = async () => {
    setMsg(""); setErr(""); setCreatedEvent(null);

    const tn = sanitizeTN(value);
    const vErr = validateTN(tn);
    if (vErr) { setErr(vErr); return; }

    setSubmitting(true);
    try {
      // POST /orders/scan/ -> объект заказа + created_event
      const { data } = await api.post(SCAN_URL, { tracking_number: tn /*, description: "" */ });
      setMsg("Скан успешно добавлен.");

      if (data?.created_event) {
        setCreatedEvent({
          status: data.created_event.status || "",
          location: data.created_event.location || "",
          timestamp: data.created_event.timestamp || ""
        });
      }

      // сохранить в историю
      try {
        const raw = localStorage.getItem(LS_TRACKS);
        const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
        const next = uniq([tn, ...arr]).slice(0, 200);
        localStorage.setItem(LS_TRACKS, JSON.stringify(next));
        setHistory(next);
      } catch {}

      setValue("");
    } catch (e2) {
      const res = e2?.response;
      const d = res?.data || {};
      let text =
        d?.detail ||
        (Array.isArray(d?.tracking_number) && d.tracking_number[0]) ||
        (typeof d?.error === "string" && d.error) ||
        "";

      // дружелюбные дефолтные сообщения
      if (res?.status === 429) text = text || "Слишком много попыток. Попробуйте позже.";
      if (res?.status >= 500) text = text || "Сервис временно недоступен. Попробуйте позже.";
      if (!text) text = "Не удалось добавить скан. Проверьте трек-номер и попробуйте ещё раз.";

      setErr(text);
    } finally {
      setSubmitting(false);
    }
  };

  const onSubmit = (e) => { e.preventDefault(); doSubmit(); };

  return (
    <div className="parcels">
      <div className="parcels__card">
        {/* логотип 66px — копия дизайна */}
        <div className="parcels__logo"><img src={logo} alt="Lider Cargo" /></div>

        <form className="parcels__block" onSubmit={onSubmit} noValidate>
          <div className="parcels__block-title">Добавить трек-номер</div>

          <div className="parcels__inputs" aria-live="polite">
            <TrackInput
              value={value}
              onChange={(v) => { setValue(v); if (err) setErr(""); }}
              suggestionsPool={suggestionsPool}
              onEnter={doSubmit}
              hasError={!!err}
            />
          </div>

          <button className="parcels__btn" type="submit" disabled={submitting}>
            Добавить
          </button>

          <div className="parcels__hint">Длина трек-номера {TN_MIN}–{TN_MAX} символов, без пробелов.</div>

          {msg && <div className="parcels__success">{msg}</div>}
          {err && <div className="parcels__error">{err}</div>}

          {/* Блок с созданным событием из ответа swagger */}
          {createdEvent && (
            <div className="parcels__results" style={{marginTop: 8}}>
              <div className="parcels__results-title">Создано событие</div>
              <div className="parcels__bucket">
                <div className="parcels__bucket-list">
                  <span className="parcels__tag"><b>Статус:</b>&nbsp;{createdEvent.status || "—"}</span>
                  {createdEvent.location ? (
                    <span className="parcels__tag"><b>Локация:</b>&nbsp;{createdEvent.location}</span>
                  ) : null}
                  {createdEvent.timestamp ? (
                    <span className="parcels__tag"><b>Время:</b>&nbsp;{createdEvent.timestamp}</span>
                  ) : null}
                </div>
              </div>
            </div>
          )}
        </form>
      </div>
    </div>
  );
};

export default Parcelsscan;
