// src/components/Parcels/Parcels.jsx
import React, { useEffect, useMemo, useRef, useState } from "react";
import api from "../Api/Api";
import logo from "../logo/logo.png";

/** ===== API ===== */
const ORDERS_URL = "orders/";
const FIND_URL   = "orders/find/";
const CLAIM_URL  = "orders/claim/";
const TRACK_URL  = (tn) => `orders/track/${encodeURIComponent(tn)}/`;

/** ===== utils ===== */
const norm  = (s) => String(s ?? "").trim();
const uniq  = (arr) => Array.from(new Set(arr.map(String)));
const clamp = (n, a, b) => Math.max(a, Math.min(b, n));
const sanitizeTN = (s) => norm(String(s).replace(/\s+/g, "")); // убираем пробелы внутри
const TN_MIN = 1;
const TN_MAX = 32;

/** локальное хранилище для подсказок */
const LS_TRACKS = "lc_tracks";

/** 4 стадии (подписи) */
const STAGES = [
  { key: "accepted", label: "Принят на склад в китае" },
  { key: "sent",     label: "Отправлен из китая" },
  { key: "arrived",  label: "Прибыл в пункт выдачи" },
  { key: "done",     label: "Получен" },
];

/** универсальное извлечение статуса из объекта заказа (логика) */
function extractStatus(order) {
  let s =
    (order?.last_status ??
      order?.status ??
      (Array.isArray(order?.events) && order.events.length
        ? order.events[order.events.length - 1]?.status
        : "")) || "";
  s = String(s).toLowerCase();

  const compact = s.replace(/\s+/g, "_");
  const plain = s.replace(/[^a-zа-яё]+/gi, " ");

  if (/accepted_in_china|accepted|принят/.test(compact) || /склад.*кита/.test(plain)) return "accepted";
  if (/sent_from_china|sent|отправлен/.test(compact) || /отправлен.*кита/.test(plain)) return "sent";
  if (/arrived_to_pvz|arrived/.test(compact) || /прибыл.*пункт.*выда/.test(plain)) return "arrived";
  if (/received|delivered|получен/.test(compact)) return "done";

  return null;
}

/* ====== Typeahead инпут (верстка/классы + UX) ====== */
const TrackInput = ({ value, onChange, suggestionsPool }) => {
  const [open, setOpen] = useState(false);
  const [serverHints, setServerHints] = useState([]);
  const [visible, setVisible] = useState(12);
  const wrapRef = useRef(null);

  // закрытие по клику вне и по Esc
  useEffect(() => {
    const onDoc = (e) => { if (wrapRef.current && !wrapRef.current.contains(e.target)) setOpen(false); };
    const onEsc = (e) => { if (e.key === "Escape") setOpen(false); };
    document.addEventListener("mousedown", onDoc);
    document.addEventListener("keydown", onEsc);
    return () => {
      document.removeEventListener("mousedown", onDoc);
      document.removeEventListener("keydown", onEsc);
    };
  }, []);

  useEffect(() => {
    let alive = true;
    const q = norm(value);
    if (!q) { setServerHints([]); setVisible(12); return; }
    (async () => {
      try {
        // Ищем по tracking_number (а не по q)
        const { data } = await api.get(FIND_URL, { params: { tracking_number: q } });
        if (!alive) return;
        const list = Array.isArray(data) ? data : (Array.isArray(data?.results) ? data.results : []);
        const hints = list
          .map((x) => (typeof x === "string" ? x : x?.tracking_number))
          .filter(Boolean);
        setServerHints(hints);
        setVisible(12);
      } catch {/* no-op */}
    })();
    return () => { alive = false; };
  }, [value]);

  const suggestions = useMemo(() => {
    const base = uniq([...(suggestionsPool || []), ...(serverHints || [])]);
    const q = norm(value).toLowerCase();
    return q ? base.filter((t) => String(t).toLowerCase().includes(q)) : base;
  }, [suggestionsPool, serverHints, value]);

  const shown = useMemo(() => suggestions.slice(0, visible), [suggestions, visible]);

  const onScroll = (e) => {
    const el = e.currentTarget;
    if (el.scrollTop + el.clientHeight >= el.scrollHeight - 6) {
      setVisible((v) => clamp(v + 12, 0, suggestions.length));
    }
  };

  return (
    <div className="parcels__track-row" ref={wrapRef}>
      <input
        className="parcels__input"
        placeholder="Трек-номер"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        onFocus={() => setOpen(true)}
        autoComplete="off"
        inputMode="text"
        maxLength={TN_MAX}
        aria-expanded={open}
        aria-haspopup="listbox"
      />
      {open && (
        <div className="parcels__menu" role="listbox" onMouseLeave={() => setOpen(false)}>
          <div className="parcels__list" onScroll={onScroll}>
            {shown.length === 0 ? (
              <div className="parcels__empty">Нет подсказок</div>
            ) : shown.map((s, i) => (
              <div
                key={`${s}-${i}`}
                className="parcels__option"
                role="option"
                onMouseDown={() => { onChange(String(s)); setOpen(false); }}
              >
                {s}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

/* ====== Модалка (верстка/классы) ====== */
const Modal = ({ open, title, loading, error, items, onClose }) => {
  useEffect(() => {
    if (!open) return;
    const onKey = (e) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", onKey);
    return () => document.removeEventListener("keydown", onKey);
  }, [open, onClose]);

  if (!open) return null;
  return (
    <div className="modal" role="dialog" aria-modal="true" aria-label={title}>
      <div className="modal__backdrop" onClick={onClose} />
      <div className="modal__dialog">
        <div className="modal__header">
          <div className="modal__title">{title}</div>
          <button className="modal__close" onClick={onClose} aria-label="Закрыть">×</button>
        </div>
        <div className="modal__body">
          {loading && <div className="modal__muted">Загрузка…</div>}
          {error && <div className="modal__error">{error}</div>}
          {!loading && !error && items.length === 0 && (
            <div className="modal__muted">Нет заказов с этим статусом.</div>
          )}
          {!loading && !error && items.length > 0 && (
            <div className="modal__list">
              {items.map((o) => (
                <div key={o.id ?? o.tracking_number ?? Math.random()} className="parcelcard">
                  <div className="parcelcard__row"><b>Трек:</b> <span>{o.tracking_number || "—"}</span></div>
                  <div className="parcelcard__row"><b>Статус:</b> <span>{o.last_status || o.status || "—"}</span></div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

// стало — добавили hideCount
const StageCard = ({ label, count, onClick, hideCount = false }) => (
  <button type="button" className="parcels__stage" aria-label={label} onClick={onClick}>
    <div className="parcels__stage-label">{label}</div>
    {!hideCount && <span className="parcels__badge" aria-hidden>{count || 0}</span>}
  </button>
);

/* список результатов по стадиям (логика + верстка) */
const BucketList = ({ title, items }) => {
  if (!items || items.length === 0) return null;
  return (
    <div className="parcels__bucket">
      <div className="parcels__bucket-title">{title} — {items.length}</div>
      <div className="parcels__bucket-list">
        {items.map((t) => <span className="parcels__tag" key={t}>{t}</span>)}
      </div>
    </div>
  );
};

/* ====== Главный компонент (сшивка логики и верстки) ====== */
const Parcels = () => {
  const [globalCounts, setGlobalCounts] = useState({ accepted: 0, sent: 0, arrived: 0, done: 0 });

  // динамические поля
  const [tracks, setTracks] = useState([""]);
  const [history, setHistory] = useState([]);
  const [bucket, setBucket] = useState({ accepted: [], sent: [], arrived: [], done: [] });
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");
  const [submitting, setSubmitting] = useState(false);

  // модалка
  const [mOpen, setMOpen] = useState(false);
  const [mTitle, setMTitle] = useState("");
  const [mLoading, setMLoading] = useState(false);
  const [mError, setMError] = useState("");
  const [mItems, setMItems] = useState([]);

  /* история из LS */
  useEffect(() => {
    try {
      const raw = localStorage.getItem(LS_TRACKS);
      const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
      setHistory(arr);
    } catch { setHistory([]); }
  }, []);

  /* counters via pagination, логика на extractStatus */
  const accumulate = (acc, results) => {
    results.forEach((o) => {
      const key = extractStatus(o);
      if (key && acc[key] !== undefined) acc[key] += 1;
    });
  };

  const fetchCounters = async () => {
    const totals = { accepted: 0, sent: 0, arrived: 0, done: 0 };
    let url = ORDERS_URL;
    let guard = 0;
    try {
      while (url && guard < 100) {
        const { data } = await api.get(url);
        const pageResults = Array.isArray(data?.results) ? data.results : (Array.isArray(data) ? data : []);
        accumulate(totals, pageResults);
        url = data?.next || null;
        guard += 1;
      }
      setGlobalCounts(totals);
    } catch (e) {
      const code = e?.response?.status;
      if (code === 401 || code === 403) setError("Требуется вход в аккаунт, чтобы увидеть ваши заказы.");
    }
  };
  useEffect(() => { fetchCounters(); }, []);

  /* открыть модалку по одному треку (логика) */
  const openTrackModal = async (tn) => {
    const title = `Трек: ${tn}`;
    setMTitle(title);
    setMItems([]); setMError(""); setMLoading(true); setMOpen(true);
    try {
      const { data } = await api.get(TRACK_URL(tn));
      const order = data?.order || data;
      if (!order || Object.keys(order).length === 0) {
        setMError("Это не ваш товар или товар не найден.");
      } else {
        const normalized = {
          tracking_number: order.tracking_number || tn,
          last_status: order.last_status || order.status || "",
          ...order,
        };
        setMItems([normalized]);
      }
    } catch {
      setMError("Это не ваш товар или товар не найден.");
    } finally {
      setMLoading(false);
    }
  };

  /* открыть модалку со списком заказов для конкретной стадии (логика + верстка) */
  const openStageModal = async (key) => {
    const stage = STAGES.find((s) => s.key === key);
    setMTitle(stage ? stage.label : "Детали");
    setMItems([]); setMError(""); setMLoading(true); setMOpen(true);

    let url = ORDERS_URL;
    let guard = 0;
    const matched = [];

    try {
      while (url && guard < 60 && matched.length < 300) {
        const { data } = await api.get(url);
        const list = Array.isArray(data?.results) ? data.results : (Array.isArray(data) ? data : []);
        for (const o of list) if (extractStatus(o) === key) matched.push(o);
        url = data?.next || null;
        guard += 1;
      }
      setMItems(matched);
    } catch {
      setMError("Не удалось загрузить заказы. Попробуйте позже.");
    } finally {
      setMLoading(false);
    }
  };

  /* если пришли с ?track=... — сразу показать модалку и сохранить в историю */
  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const tn = norm(params.get("track"));
    if (tn) {
      try {
        const raw = localStorage.getItem(LS_TRACKS);
        const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
        const next = uniq([tn, ...arr]).slice(0, 200);
        localStorage.setItem(LS_TRACKS, JSON.stringify(next));
        setHistory(next);
      } catch {}
      setTracks([tn, ""]);
      openTrackModal(tn);
    }
  }, []);

  const suggestionsPool = useMemo(() => history, [history]);

  // динамическое добавление следующего поля
  const setTrack = (i, v) =>
    setTracks((arr) => {
      const next = arr.map((x, idx) => (idx === i ? v : x));
      if (i === arr.length - 1 && norm(v)) next.push("");
      while (next.length > 1 && next.at(-1) === "" && next.at(-2) === "") next.pop();
      return next;
    });

  // валидация одного трека
  const validateTN = (tn) => {
    if (!tn) return "Введите трек-номер.";
    if (tn.length < TN_MIN || tn.length > TN_MAX) return `Длина трек-номера: ${TN_MIN}-${TN_MAX} символов.`;
    return "";
  };

  /**
   * ОТПРАВКА ФОРМЫ:
   * — НЕ делаем scan;
   * — Делаем POST /orders/claim/ чтобы «привязать» заказ к аккаунту;
   * — Для группировки по стадиям используем статус из ответа claim (order/last_status или created_event.status).
   */
  const submit = async (ev) => {
    ev.preventDefault();
    setMessage(""); setError("");

    const prepared = uniq(
      tracks
        .map((t) => sanitizeTN(t))
        .filter(Boolean)
    );

    if (prepared.length === 0) { setError("Введите трек-номер."); return; }

    // валидация всех треков до запросов
    const bad = prepared
      .map((tn) => ({ tn, err: validateTN(tn) }))
      .filter((x) => x.err);
    if (bad.length) {
      setError(`${bad[0].tn}: ${bad[0].err}`);
      return;
    }

    setSubmitting(true);
    const ok = [];
    let hasFails = false;

    for (const tn of prepared) {
      try {
        // Главное изменение: КЛЕЙМ вместо проверки/сканирования
        const payload = { tracking_number: tn };
        const { data } = await api.post(CLAIM_URL, payload);
        ok.push({ tn, data });
      } catch {
        hasFails = true;
      }
    }

    // сохранить удачные треки в историю
    if (ok.length) {
      try {
        const raw = localStorage.getItem(LS_TRACKS);
        const arr = Array.isArray(JSON.parse(raw)) ? JSON.parse(raw) : [];
        const next = uniq([...ok.map(x => x.tn), ...arr]).slice(0, 200);
        localStorage.setItem(LS_TRACKS, JSON.stringify(next));
        setHistory(next);
      } catch {}
    }

    // бакеты статусов (берём из ответа claim)
    const group = { accepted: [], sent: [], arrived: [], done: [] };
    for (const { tn, data } of ok) {
      const order = data?.order || data;
      const createdEvent = data?.created_event;
      const statusStr = String(
        order?.last_status ||
        order?.status ||
        createdEvent?.status ||
        ""
      );
      const key = extractStatus({ last_status: statusStr, events: order?.events || [] });
      if (key) group[key].push(tn);
    }
    setBucket(group);

    await fetchCounters();

    // итоговые сообщения
    if (ok.length === 0) {
      setMessage("");
      setError("Это не ваш товар или товар не найден.");
    } else {
      setMessage(`Добавлено в ваш аккаунт: ${ok.length}.`);
      if (hasFails) setError("Часть треков не удалось добавить. Возможно, это не ваш товар или товар не найден.");
    }

    // если один трек успешно добавлен — открыть по нему модалку
    if (ok.length === 1 && prepared.length === 1) {
      openTrackModal(ok[0].tn);
    }

    setTracks([""]);
    setSubmitting(false);
  };

  return (
    <div className="parcels">
      <div className="parcels__card">
        <div className="parcels__logo"><img src={logo} alt="Lider Cargo" /></div>

        <form className="parcels__block" onSubmit={submit} noValidate>
          <div className="parcels__block-title">Добавить трек-номер</div>

          <div className="parcels__inputs" aria-live="polite">
            {tracks.map((t, i) => (
              <TrackInput
                key={i}
                value={t}
                onChange={(v) => setTrack(i, v)}
                suggestionsPool={suggestionsPool}
              />
            ))}
          </div>

          <button className="parcels__btn" type="submit" disabled={submitting}>
            Добавить
          </button>

          <div className="parcels__hint">Длина трек-номера {TN_MIN}–{TN_MAX} символов, без пробелов.</div>
          {message && <div className="parcels__success">{message}</div>}
          {error && <div className="parcels__error">{error}</div>}
        </form>

        {/* четыре карточки со счётчиками — кликабельные */}
<div className="parcels__grid" aria-live="polite">
  <StageCard label={STAGES[0].label} count={globalCounts.accepted} onClick={() => openStageModal("accepted")} />
  <StageCard label={STAGES[1].label} count={globalCounts.sent}     onClick={() => openStageModal("sent")} />
  <StageCard label={STAGES[2].label} count={globalCounts.arrived}  onClick={() => openStageModal("arrived")} />
  <StageCard label={STAGES[3].label} onClick={() => openStageModal("done")} hideCount />
</div>

        {(bucket.accepted.length + bucket.sent.length + bucket.arrived.length + bucket.done.length > 0) && (
          <div className="parcels__results">
            <div className="parcels__results-title">Результаты добавления</div>
            <BucketList title={STAGES[0].label} items={bucket.accepted} />
            <BucketList title={STAGES[1].label} items={bucket.sent} />
            <BucketList title={STAGES[2].label} items={bucket.arrived} />
            <BucketList title={STAGES[3].label} items={bucket.done} />
          </div>
        )}
      </div>

      <Modal
        open={mOpen}
        title={mTitle}
        loading={mLoading}
        error={mError}
        items={mItems}
        onClose={() => setMOpen(false)}
      />
    </div>
  );
};

export default Parcels;
